.contact{
    padding: 20px;

}
.contact__content{
    padding: 20px;

}

.contact_content_header-text{
    color: var(--yellow-theme-sub-text-color);
    display: inline-block;
    font-size: 22px;
    font-weight: 600;
    line-height: 1em;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    padding: 5px 10px;
    border-bottom: 2px solid var(--yellow-theme-main-color);

}
.contact_content_header-text::before,
.contact_content_header-text::after{
    position: absolute;
    content: "";
    width: 2px;
    height: 5px;
    background-color: var(--yellow-theme-main-color);
    bottom: -1px;

}
.contact_content_header-text::before{
    left: 0;
}
.contact_content_header-text::after{
    right: 0;
}


.contact_content_form{
    margin-top: 30px;

}
.contact_content_form_controls-wrapper{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
    margin-bottom: 30px;

}

.contact_content_form_controls-wrapper div{
    position: relative;
}

.inputName, .inputEmail, .inputMessage{
    width: 100%;
    border: 2px solid var(--yellow-theme-main-color);
    padding: 16px 20px;
    transition: all 0.5s ease;
    position: relative;
    background-color: transparent;
    color: var(--yellow-theme-sub-text-color);


}

.nameLabel, .emailLabel, .messageLabel{
    font-weight: 500;
    position: absolute;
    top: 7px;
    left: 12px;
    transition: all 0.2s ease;
    color: var(--yellow-theme-sub-text-color);
    font-size: 16px;

}

.nameLabel:focus, .emailLabel:focus, .messageLabel:focus{
    outline: none;

}
.inputName:focus + .nameLabel,
.inputName:valid + .nameLabel,.inputEmail:focus + .emailLabel,
.inputEmail:valid + .emailLabel,.inputMessage:focus + .messageLabel,
.inputMessage:valid + .messageLabel{
    font-size: 18px;
    top: -25px;
    left: 0px;
}


.contact_content_form button{
    cursor: pointer;
    padding: 10px 65px;
    font-size: 2rem;
    background: transparent;
    color: var(--yellow-theme-main-color);
    border: 1.5px solid var(--yellow-theme-main-color);
    border-radius: 4px;
    font-weight: 500;
}
button:hover{
    background: var(--yellow-theme-main-color);
    color: #000000;
}

@media only screen and (max-width: 768px){
    .contact_content_form {
        text-align: center;

    }
    .contact_content_form_controls-wrapper{
        grid-template-columns: repeat(1, 1fr);
    }
}